import {
  Box,
  HStack,
  VStack,
  Image,
  Text,
  Heading,
  Button,
} from "@chakra-ui/react";

export function Home() {
  return (
    <>
      <Box>
        <HStack
          // border={"1px solid green"}
          justifyContent="space-evenly"
          width="88%"
          margin={"auto"}
          mt="50px"
        >
          <VStack
            //   border={"1px solid red"}
            alignItems={"flex-start"}
          >
            <Text fontSize={{ base: "md", sm: "2xl", md: "4xl" }}>
              Hi👋, I'm
            </Text>
            <Heading
              fontSize={{ base: "xl", sm: "3xl", md: "6xl" }}
              pt="5px"
              pb="5px"
            >
              Aman Trivedi
            </Heading>
            <Text
              fontSize={{ base: "md", sm: "2xl", md: "4xl" }}
              paddingBottom="-30px"
            >
              Full Stack Developer
            </Text>
            <Text
              align={"start"}
              fontSize={{ base: "10px", sm: "15px", md: "sm" }}
              style={{ marginTop: "0px" }}
            >
              Intermidiate at - ReactJS, NodeJS, MongoDB. love to play with
              DOMS.
              <br /> Cloning some E-commerse Websites{" "}
            </Text>
            <HStack>
              <Button
                fontSize={["12px", "12px", "18px"]}
                width={["60px", "60px", "100px"]}
                color={"black"}
                bgColor="blue.300"
                _hover={"none"}
              >
                Hire Me
              </Button>
              <Button
                width={["60px", "60px", "100px"]}
                fontSize={["12px", "12px", "18px"]}
                onClick={() => {
                  return window.open(
                    "https://drive.google.com/file/d/1tFcj6z9DFDZyKf7UC7Z76iggcaVZayAN/view",
                    "_blank"
                  );
                }}
              >
                Resume
              </Button>
            </HStack>
          </VStack>
          <Box>
            <Image
              src={require("../files/profile.jpg")}
              borderRadius="40px"
              width={["150px", "150px", "250px"]}
            />
          </Box>
        </HStack>
      </Box>
    </>
  );
}
